import React, { useState } from 'react';
import CryptoJS from 'crypto-js';
import { Button } from "react-bootstrap";
import '../styles/SignUp.css'
import Header from '../component/Header';
import Logo from '../component/Logo';
// import { duplicateId, duplicateNickname, onClickSignUp } from '../utils/api/userAPI';
import Api from "../utils/api/userAPI";

const SignUp = () => {
    const [inputId, setInputId] = useState('')
    let [inputPw, setInputPw] = useState('')
    const [inputNickName, setInputNickName] = useState('')

    const handleInputId = (e) => {
        setInputId(e.target.value)
    }

    const handleInputPw = (e) => {
        setInputPw(e.target.value)
    }

    const handleInputNickName = (e) => {
        setInputNickName(e.target.value)
    } 

    const userSignUpData = {
        "id": inputId,
        "pw": CryptoJS.SHA256(inputPw).toString(),
        "nickName": inputNickName,
    }

    const samePassword = () => {
        var firstInput = document.getElementById("first-pw");
        var secondInput = document.getElementById("second-pw");
        
        if(firstInput.value === secondInput.value) {
            alert(" .")
        } else {
            alert("  .")
            secondInput.value = null
            secondInput.focus();
        }
    }

    return (
        <div className="SignUpArea">
            <Header></Header>
            <Logo></Logo>
            <div className="UserInfoArea">
                <table>
                    <tr>
                        <th colspan="1"></th>
                        <td colspan="1">
                            <input class="userInfo" type="text" placeholder="" id="id" name="id" value={inputId} onChange={handleInputId}/>
                            <Button variant="secondary" size="sm" className="subBtn" onClick={Api.duplicateId.bind(this, inputId)}><br/></Button>
                        </td>
                    </tr>
                    <tr>
                        <th colspan="1"></th>
                        <td colspan="1">
                            <input class="userInfo" type="password" placeholder="" id="first-pw" name="pw" value={inputPw} onChange={handleInputPw}/>
                        </td>
                    </tr>
                    <tr>
                        <th colspan="1"></th>
                        <td colspan="1">
                            <input class="userInfo" type="password" id="second-pw" placeholder=" "/>
                            <Button variant="secondary" size="sm" className="subBtn" onClick={samePassword}><br/></Button>
                        </td>
                    </tr>
                    <tr>
                        <th colspan="1"></th>
                        <td colspan="1">
                            <input class="userInfo" type="text" placeholder="" id="nickName" name="nickName" value={inputNickName} onChange={handleInputNickName}/>
                            <Button variant="secondary" size="sm" className="subBtn" onClick={ Api.duplicateNickname.bind(this, inputNickName) }><br/></Button>
                        </td>
                    </tr>
                </table>
                <Button variant="danger" id="signup-btn" onClick={Api.onClickSignUp.bind(this, userSignUpData)}>SIGN UP</Button>
            </div>
            
        </div>
    );
};

export default SignUp;