import React from 'react'
import _ from 'lodash'
import { Link } from 'react-router-dom'

const Pagenation = (props) => {
  const { currentPage, totalPage, onPageChange } = props; //  () ,    () 

  // if (totalPage <= 1 ) return null; // 1     

  const pages = _.range(0, totalPage); //      +1, https://lodash.com/docs/#range 

  return (
    <nav> {/* VSCode : nav>ul.pagination>li.page-item>a.page-link */}
      <ul className="pagination">
        {pages.map(page => (
          <li
            key={page}
            className={page === currentPage ? "page-item active" : "page-item"} // Bootstrap     
            style={{ cursor: "pointer" }}>
            <a className="page-link" onClick={() => onPageChange(page)}>{page + 1}</a>
          </li>
        ))}
      </ul>
    </nav>
  );
}

export default Pagenation;