import React, { Component } from 'react';
import { Route, Link } from 'react-router-dom'
import { Button } from 'react-bootstrap'
import * as Home from '../services/home.js'
import '../styles/HeaderLogo.css'
import { getCookie, removeCookie } from '../utils/Cookie';


class SideBar extends Component {
    render() {
        const logout = () => {
            removeCookie("access-token")
            removeCookie("user-id")
            localStorage.removeItem("refresh-token")
            window.location.href = "/"
        }

        if (getCookie("access-token") != null) {
            return (
                <div className="SideBar">
                    <div id="sidenav">
                        <a id="closebtn" href="javascript:void(0)" onClick={Home.closeNav}>&times;</a>
                        <div className="userInfoSideBar">
                            {getCookie('user-id')} <Button variant="danger" onClick={logout}></Button>
                        </div>
                        <Link to="/" ></Link>
                        <Link to="/community/main" > </Link>
                        <Link to="/community/free" >  </Link>
                    </div>
                    <span onClick={Home.openNav}>&#9776;</span><span id="semi-logo" onClick={Home.goHome}> MIM</span>
                </div>
            );
        }
        else {
            return (
                <div className="SideBar">
                    <div id="sidenav">
                        <a id="closebtn" href="javascript:void(0)" onClick={Home.closeNav}>&times;</a>
                        <Link to="/login"></Link>
                        <Link to="/" ></Link>
                        <Link to="/community/main" > </Link>
                        <Link to="/community/free" >  </Link>
                    </div>
                    <span onClick={Home.openNav}>&#9776;</span><span id="semi-logo" onClick={Home.goHome}> MIM</span>
                </div>
            );
        }
    }
}

export default SideBar;